import React , {Component} from 'react';
import { Link, animateScroll as scroll } from "react-scroll";

class NavigationBar extends Component {
  render() {
    return (
      <nav id="nav-wrap">
        <a className="mobile-btn" href="#nav-wrap" title="Show navigation">Show navigation</a>
        <a className="mobile-btn" href="#home" title="Hide navigation">Hide navigation</a>
        <ul id="nav" className="nav">
          <li><Link activeClass="current" to="home" spy={true} smooth={true} offset={-70} duration={500} >Home</Link></li>
          <li><Link activeClass="current" to="about" spy={true} smooth={true} offset={-70} duration={500} >About</Link></li>
          <li><Link activeClass="current" to="resume" spy={true} smooth={true} offset={-70} duration={500} >Resume</Link></li>
          <li><Link activeClass="current" to="projects" spy={true} smooth={true} offset={-70} duration={500} >Projects</Link></li>
          <li><Link activeClass="current" to="footer" spy={true} smooth={true} offset={-70} duration={500} >Contact</Link></li>
         </ul>
      </nav>
    );
  }
}

export default NavigationBar;
