import React, {Component} from 'react';

class RoboconBlog extends Component {
  render() {
    return (
      <section id="robocon-blog" className="robocon-blog">
        <div className="blog">
          <h2> Representing India at ABU Robocon</h2>
        </div>
      </section>
    );
  }
}

export default RoboconBlog;
