import React, {Component} from 'react';

class Resume extends Component {
  render() {
    return (
      <section id="resume" className="resume" name="resume">
        <div className="education">
          <div className="one column">
            <h2> Education </h2>
          </div>
          <div className="five column">
            <h2> Georgia Institute of Technology, Atlanta </h2>
            <ul>
              <li>I graduated with a Masters in Electrical and Computer Engineering </li>
              <li>At Georgia Tech, I spent most of my time learning about Machine Learning and Deep Learning and their applications to vision problems</li>
              <li>I worked in the <a href="https://ghassanalregib.info/">Olives Labs</a> for a semester researching about the robustness of computer vision models to adversarial attacks</li>
              <li>Courses: <a href="https://jrom.ece.gatech.edu/mfml-f20/">Math. Foundations for ML</a>, <a href="http://anderson.ece.gatech.edu/ece6254/">Stat. Machine learning </a>, <a href="https://www.cc.gatech.edu/~hays/compvision/">Computer Vision</a>, Computer Vision and Language </li>
            </ul>
            <h2> Veermata Jijabai Technological Institute, Mumbai </h2>
            <ul>
              <li>I graduated with a Bachelors Degree in Electronics and Telecommunications Engineering </li>
            </ul>
          </div>
        </div>
        <div className="work">
          <div className="one column">
            <h2> Work </h2>
          </div>
          <div className="five column">
            <h2> Machine Learning Engineer, Landing AI </h2>
            <ul>
              <li>Built an end-to-end action recognition pipeline that could be trained with as little as 1 hour of video data.</li>
              <li>Worked on data labeling pipelines to get data labeled using AWS Sagemaker GroundTruth and MTurk.</li>
              <li>Worked on converting FP32 Tensorflow models to FP16 TensorRT engines, which helped reduce the inference time and decreased the memory footprint of the overall ML pipeline by about 20%.</li>
              <li>Worked on object detection model to detect region of interest in images for a visual inspection system.</li>
              <li>Helped build the infrastructure for continuous deployment and monitioring of the visual inspection system running on an edge device.</li>
            </ul>
            <h2> Deep Learning Engineer, MathWorks </h2>
            <ul>
              <li>Built a labeling tool for time series signals and embedded into the signal analyzer toolbox in MATLAB, the system is part of <a href="https://www.mathworks.com/help/signal/ug/using-signal-labeler.html">2019a MATLAB release</a>.</li>
              <li>Compared the performance of LSTM based model using preprocessed and raw time-series signal in MATLAB</li>
            </ul>
            <h2> Software Engineer, Cimpress </h2>
            <ul>
              <li>Designed and developed highly scalable micro-services in NodeJS that help integrate different business units into the Cimpress platform.</li>
              <li>Integrated AWS Cloudwatch with the micro-services for logging events. These events were then pushed to SumoLogic for continuous monitoring.</li>
              <li>Developed dashboards on SumoLogic to monitor parameters like the number of hits to the services, types of errors, failure of 3rd party services, etc.</li>
              <li>Developed a web-based application using AngularJS for quality assessment to be used in the manufacturing unit of the company. The application displays details about the product and log events about the quality of the product into the database.</li>
              <li>Developed automated testing scripts for the services: integration testing, unit testing.</li>
              <li>Deployed application on the AWS infrastructure (Elastic BeanStalk, EC2, S3, Lambda).</li>
            </ul>
          </div>
        </div>
      </section>
    );
  }
}
export default Resume;
