import React, {Component} from 'react';

class About extends Component {
  render() {
    return (
      <section id="about" name="about">
        <div className="about">
          <div className="one column">
            <img className="profilepic" src="static/profile-pic.jpg"></img>
          </div>
          <div className="three column">
            <h2> About Me </h2>
            <p> I am a techie who likes trying new things. Sometimes I succeed at those and sometimes I fail to pull them off (like learning how to play ukulele :(). Off late I have been trying to learn how to make desserts every other weekend. I like to travel and have a 20-20 dream - visit atleast 20 countries and 20 US states before I turn 35 (Reach out to me in case you are curious, why those numbers). In this endeavour, I have managed to tick off 2 countries and about 14 US states (not bad right!).</p>
            <p> I belong to the group of people who believe that AI technology can revolutionize the world. With this thought, most of my work right from Undergraduate days have been in Robotics, Machine Learning and Deep Learning. At Amazon, I am currently working towards optimizing Machine learning models for lower latencies.</p>
          </div>
        </div>
      </section>
    );
  }
}
export default About;
