import React, {Component} from 'react';
import { Link, animateScroll as scroll } from "react-scroll";
import NavigationBar from './navigationBar.js';

class Header extends Component {
  constructor(props) {
    super(props);
    this.state = {
      height: 1005
    };
  }
  componentDidMount() {
    const height = window.innerHeight;
    this.setState({height: height});
  }
  render() {
    const style = {
      height: this.state.height + "px"
    };
    return (
      <section id="home" className="header" style={style}>
        <NavigationBar/>
        <div className="banner">
          <h1> Rahul Solanki </h1>
        </div>
        <div className="scrolldown">
           <Link activeClass="arrowdown" to="about" spy={true} smooth={true} offset={-70} duration={500} ><i style={{color: "white"}} className="down-icon fa fa-chevron-circle-down"></i></Link>
        </div>
      </section>
    );
  }
}
export default Header;
