import React, {Component} from 'react';
import RoboconBlog from './roboconBlog.js';
import { Router, Switch, Route } from "react-router-dom";
import Card from 'react-bootstrap/Card';
import Button from 'react-bootstrap/Button';
import history from '../history.js';

class Projects extends Component {
  render() {
    const style = {
      width: '18rem',
      margin:'30px'
    }
    return (
      <section id="projects">
        <div className="projects">
          <Card style={style}>
            <Card.Img variant="top" src="static/vqa.jpg" style={{height: "150px"}}/>
            <Card.Body>
              <Card.Title style={{height: "120px"}}>Towards compositionality in Visual Question Answering</Card.Title>
              <Card.Text style={{height: "150px"}}>
                The project aims at making the model learn different concepts like color, objects, number, etc. thereby improving the ability of the model to answer questions more accurately.
              </Card.Text>
              <Button style={{margin:'3px'}} href="https://www.youtube.com/watch?v=sdgOmvajnYU" variant="primary">Video</Button>
            </Card.Body>
          </Card>
          <Card style={style}>
            <Card.Img variant="top" src="static/fgsm.jpeg" style={{height: "150px"}}/>
            <Card.Body>
              <Card.Title style={{height: "120px"}}>Adversarial attack on Semantic Segmentation models</Card.Title>
              <Card.Text style={{height: "150px"}}>
                An experiment to attack segmentation model (PSPNet) with white box adversarial attack.
              </Card.Text>
              <Button style={{margin:'3px'}} href="https://medium.com/@solankirahul411/adversarial-attack-for-semantic-segmentation-model-e1a73b1f615e" variant="primary">Blog</Button>
              <Button style={{margin:'3px'}} href="https://github.com/rahul411/Adversarial-Example---FGSM" variant="primary">GitHub</Button>
            </Card.Body>
          </Card>
          <Card style={style}>
            <Card.Img variant="top" src="static/recommenders_systems.jpg" style={{height: "150px"}}/>
            <Card.Body>
              <Card.Title style={{height: "120px"}}>Collaborative Filtering for Item Recommendation using MovieLens Dataset</Card.Title>
              <Card.Text style={{height: "150px"}}>
                The project compares different methods for item recommendation task.
              </Card.Text>
              <Button style={{margin:'3px'}} href="https://github.com/rahul411/Collaborative-filtering-on-MovieLens" variant="primary">GitHub</Button>
            </Card.Body>
          </Card>
          <Card style={style}>
            <Card.Img variant="top" src="static/image_captioning.jpg" style={{height: "150px"}}/>
            <Card.Body>
              <Card.Title style={{height: "120px"}}>Image Captioning using Attention</Card.Title>
              <Card.Text style={{height: "150px"}}>
                The project aims to build a deep learning model that can generate a caption for a given image
              </Card.Text>
              <Button style={{margin:'3px'}} href="https://github.com/rahul411/Image-captioning-using-Attention" variant="primary">GitHub</Button>
            </Card.Body>
          </Card>
          <Card style={style}>
            <Card.Img variant="top" src="static/swarm.jpg" style={{height: "150px"}}/>
            <Card.Body>
              <Card.Title style={{height: "120px"}}>Swarm Robots in a Closed Loop Visual Odometry System by Using Visible Light Communication</Card.Title>
              <Card.Text style={{height: "150px"}}>
                The project aims to build a deep learning model that can generate a caption for a given image
              </Card.Text>
              <Button style={{margin:'3px'}} href="https://link.springer.com/chapter/10.1007/978-3-319-67934-1_18" variant="primary">Paper</Button>
            </Card.Body>
          </Card>
        </div>
      </section>
    );
  }
}
export default Projects;
